"use client"

import { useState, useEffect } from "react"
import { DashboardLayout } from "@/components/layout/DashboardLayout"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { toast } from "sonner"

interface Alert {
  id: string
  studentId: string
  studentName: string
  type: 'hand_raise' | 'help_needed' | 'technical_issue'
  message?: string
  timestamp: string
  resolved: boolean
}

// Format date consistently for both server and client
const formatDate = (dateString: string) => {
  try {
    return new Date(dateString).toLocaleString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit',
      hour12: true,
      timeZone: 'UTC'
    })
  } catch (error) {
    return dateString
  }
}

export default function AlertsPage() {
  const [alerts, setAlerts] = useState<Alert[]>([])
  const [loading, setLoading] = useState(true)
  const [mounted, setMounted] = useState(false)

  useEffect(() => {
    setMounted(true)
    fetchAlerts()
  }, [])

  const fetchAlerts = async () => {
    try {
      const response = await fetch('/api/alerts')
      const data = await response.json()
      setAlerts(data)
    } catch (error) {
      toast.error("Failed to fetch alerts")
    } finally {
      setLoading(false)
    }
  }

  const resolveAlert = async (alertId: string) => {
    try {
      const response = await fetch('/api/alerts', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ alertId, resolved: true }),
      })

      if (!response.ok) {
        throw new Error('Failed to resolve alert')
      }

      const updatedAlert = await response.json()
      setAlerts(prev =>
        prev.map(alert =>
          alert.id === updatedAlert.id ? updatedAlert : alert
        )
      )
      toast.success("Alert resolved")
    } catch (error) {
      toast.error("Failed to resolve alert")
    }
  }

  // Prevent hydration mismatch by not rendering until mounted
  if (!mounted) {
    return null
  }

  if (loading) {
    return (
      <DashboardLayout>
        <div className="flex items-center justify-center h-full">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900" />
        </div>
      </DashboardLayout>
    )
  }

  return (
    <DashboardLayout>
      <div className="space-y-6">
        {alerts.length === 0 ? (
          <p className="text-center text-gray-500">No active alerts</p>
        ) : (
          alerts.map((alert) => (
            <Card key={alert.id} className="p-4 flex justify-between items-center">
              <div>
                <h3 className="font-semibold">{alert.studentName}</h3>
                <p className="text-sm text-gray-600">{alert.message || alert.type.replace('_', ' ')}</p>
                <p className="text-xs text-gray-400">
                  {mounted ? formatDate(alert.timestamp) : ''}
                </p>
              </div>
              <div className="flex items-center space-x-4">
                <Badge variant={alert.resolved ? "secondary" : "destructive"}>
                  {alert.resolved ? "Resolved" : "Active"}
                </Badge>
                {!alert.resolved && (
                  <Button variant="outline" size="sm" onClick={() => resolveAlert(alert.id)}>
                    Resolve
                  </Button>
                )}
              </div>
            </Card>
          ))
        )}
      </div>
    </DashboardLayout>
  )
}
